body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.header {
  height: 40px;
  width: 100%;
  background: #673AB7;
  color: rgba(255,255,255, 0.9);
  font-size: 24px;
  padding-top: 10px;
  text-align: center;

}

.content {
  width: 800px;
  -webkit-flex: 1 1;
          flex: 1 1;
  max-width: 100%;
}

.container {
  padding-top: 10px;
}

.transFactor {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.transFactorSelect {
  width: 100%;
  height: 38px;
  border-radius: 4px;
  position: relative;
  border: 1px solid #ced4da;
  font-size: 16px;
  color: hsl(0,0%,30%);
  background-color: white;
  padding: 10px 26px 10px 12px;
  margin-bottom: 10px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; 
}

.transFactorOption {
  font-size: 16px;
  margin: 5px;
}

.DNAText {
  box-sizing:border-box;
  width: 100%;
  height: 38px;
  border-radius: 4px;
  font-size: 16px;
  padding-left: 10px;
  border: 1px solid #ced4da;
}

@media screen and (max-width: 1000px) {
  .content {
    width: 600px;
  } 
}

